import React from "react"
import SEO from "../components/seo"
import Logo from "../components/logo"
import Nav from "../components/navigation"
import WorkImage from "../images/work-image.jpg"
//import mobileWorkImage from "../images/mobile-work-image.jpg"
import GiantHeader from "../components/giantheader"
import PageInfo from "../components/pageinfo"
import { WORK_INFO } from "../components/siteinfo"
import { MAIN_TEXT_COLOR, LOGO_COLOR_DARK } from "../components/siteconfig"
import 'aos/dist/aos.css';
import './styles.css';

class Work extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isActive: true
    }
  }
  componentDidMount() {
    const AOS = require('aos');
    this.aos = AOS
    this.aos.init()
  }

  componentDidUpdate() {
    this.aos.refresh()
  }
  render() {
    return (
      <div className="workbg" css={{
        display: 'flex'

      }}>
        <SEO title="Work - 1240 Greene St" />
        <div css={{ width: '50%' }}>
          <Logo title="1240 Greene St" color={LOGO_COLOR_DARK} />
          <div data-aos="fade-right" data-aos-delay="300" data-aos-easing="ease-in-sine" data-aos-duration="500">
            <PageInfo header={WORK_INFO.header} decscription={WORK_INFO.description} color={MAIN_TEXT_COLOR} width="350px" mobileMarginTop="20%" />
          </div>
        </div>


        <div css={{ zIndex: `100`, marginLeft: `auto`, justifyContent: `flex-end` }}>
          <Nav isActive={this.state.isActive} />
          <div css={{
            position: 'fixed',
            bottom: '-117px',
            left: '-70%',
            transform: 'scale(.37)',
            '@media(min-width:700px)': {
              bottom: '-67px',
              transform: 'scale(1)',
              left: '40%'
            }
          }}>
            <div data-aos="fade-left" data-aos-delay="350" data-aos-easing="ease-in-sine" data-aos-duration="500">
              <GiantHeader text="Work" color={"#fff"} opacity=".4" stroke="1px #000" />
            </div>
          </div>
        </div>


      </div>
    )
  }
}


export default Work
